.social {
  p {
    display: none;

    @media (max-width: $width-desktop) {
      display: block;

      @include set-mono-text(14px, 24px);
      color: #1b2d37;
      text-transform: uppercase;
      padding-bottom: 2.5%;
      padding-right: 1%;
    }

    @media (max-width: $width-laptop) {
      margin-bottom: 8%;
    }
  }

  @media (max-width: $width-desktop) {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    box-sizing: border-box;
    justify-content: space-between;
    font-size: 14px;
    width: 100%;
    padding: 0 3% 0;
    border-bottom: 1px solid rgba(#626962, 0.2);
  }

  @media (max-width: $width-laptop) {
    flex-direction: column;
    align-items: center;
  }
}

.social__logo {
  margin-top: 7%;
  margin-bottom: 22%;

  .logo__title {
    font-size: 28px;
    color: #203c42;
  }

  @media (max-width: $width-desktop) {
    margin: 0;
    padding-bottom: 2.5%;
    padding-right: 1%;
  }

  @media (max-width: $width-laptop) {
    svg {
      transform: scale(1.1);
    }
  }
}

.social__list {
  @include list-refresh;
  display: flex;
  justify-content: space-between;
  width: 50%;

  @media (max-width: $width-desktop) {
    width: 15%;
    flex-wrap: wrap;
    padding-bottom: 2.5%;
  }

  @media (max-width: $width-laptop) {
    width: 38%;
    justify-content: space-around;
    padding: 0 1% 15%;
  }
}

.social__link {
  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}
