.main-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 52%;

  @media (max-width: $width-desktop) {
    width: 100%;
    box-sizing: border-box;
    padding: 3.3% 4% 5% 3%;
  }

  @media (max-width: $width-laptop) {
    padding-left: 0;
    padding-right: 0;
  }

  &--nojs {
    @media (max-width: $width-laptop) {
      border-bottom: 0;
    }

    .h2 {
      border: none;
    }

    .tab__toggle {
      display: block;
    }

    .tab__button {
      display: none;
    }

    .tab__content {
      display: block;
    }
  }
}
